<template>
  <el-card>
    <template #header>
      <span>Job Listing</span>
      <span>
        <el-row>
          <el-col :span="14">
            <el-select v-model="job_title.company_id" @change="getAllJobs()" filterable placeholder="Filter by client">
              <el-option v-for="company in $store.state.company.company_branches" :key="company"
                :label="company.company_name" :value="company.id" />
            </el-select>
          </el-col>

          <el-col :span="8">
            <el-button type="primary" class="ms-2" plain @click="dialog = true">Add New Job</el-button>
          </el-col>
        </el-row>
      </span>
    </template>
    <el-dialog v-model="dialog" title="Add Job" width="65%">
      <el-form :model="job_title" size="large" ref="job_title" label-position="top" :inline="true">
        <el-row>
          <el-col :span="8">
            <el-form-item label="Client" prop="company_id">
              <el-select v-model="job_title.company_id" filterable placeholder="Select client">
                <el-option v-for="company in $store.state.company.company_branches" :key="company"
                  :label="company.company_name" :value="company.id" />
              </el-select> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Position" prop="job_title" :rules="[
              {
                required: true,
                message: 'Job title is required',
                trigger: ['blur', 'change'],
              },
            ]">
              <el-input v-model="job_title.job_title" placeholder="Enter job title" /> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Contract Type" prop="job_type" :rules="[
              {
                required: true,
                message: 'Job type is required',
                trigger: ['blur'],
              },
            ]">
              <el-select v-model="job_title.job_type" filterable placeholder="Select job type">
                <el-option v-for="employment_type in $store.state.company.employment_types" :key="employment_type"
                  :label="employment_type.employment_type" :value="employment_type.employment_type" /></el-select>
            </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Industry" prop="industry" :rules="[
              {
                required: true,
                message: 'Job industry is required',
                trigger: ['blur'],
              },
            ]">
              <el-select v-model="job_title.industry" placeholder="Select job industry" filterable>
                <el-option :value="industry.industry" v-for="industry in $store.state.dictionary.industries"
                  :key="industry.industry" :label="industry.industry" />
              </el-select> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Career Level" prop="career_level">
              <el-select v-model="job_title.career_level" filterable placeholder="Select career level">
                <el-option v-for="level in [
                  'Entry-level',
                  'Intermediate',
                  'Mid-level',
                  'Senior or executive-level',
                ]" :key="level" :label="level" :value="level" />
              </el-select> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Years of Experience">
              <el-select v-model="job_title.experience" placeholder="Select required years of experience" filterable>
                <el-option value="0">No Experience/Less than 1 year</el-option>
                <el-option value="1 year">1 year</el-option>
                <el-option value="2 years">2 years</el-option>
                <el-option value="3 years">3 years</el-option>
                <el-option value="4 years">4 years</el-option>
                <el-option value="5 years">5 years</el-option>
                <el-option value="6 years">6 years</el-option>
                <el-option value="7 years">7 years</el-option>
                <el-option value="8 years">8 years</el-option>
                <el-option value="9 years">9 years</el-option>
                <el-option value="10 years">10 years</el-option>
                <el-option value="11 years">11 years</el-option>
                <el-option value="12 years">12 years</el-option>
                <el-option value="13 years">13 years</el-option>
                <el-option value="14 years">14 years</el-option>
                <el-option value="15 years">15 years</el-option>
                <el-option value="16 years">16 years</el-option>
                <el-option value="17 years">17 years</el-option>
                <el-option value="18 years">18 years</el-option>
                <el-option value="19 years">19 years</el-option>
                <el-option value="20 years">20 years</el-option>
                <el-option value="More than 20 years">More than 20 years</el-option>
              </el-select>
            </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Country" prop="country">
              <el-select v-model="job_title.country" filterable placeholder="Select country">
                <el-option v-for="country in $store.state.dictionary.countries" :key="country" :label="country.name"
                  :value="country.name" />
              </el-select> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Location" prop="location">
              <el-input v-model="job_title.location" placeholder="Enter location" /> </el-form-item></el-col>
          <el-col :span="8">
            <el-form-item label="Application Deadline">
              <el-date-picker v-model="job_title.application_deadline" type="date"
                placeholder="Enter application deadline" style="width: 100%" />
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="Job Description" prop="description">
              <ckeditor :editor="editor" :config="editorConfig" v-model="job_title.description" placeholder="Enter description" />
            </el-form-item></el-col>
          <el-col :span="24">
            <el-form-item label="Required Skills" prop="skills">
              <ckeditor :editor="editor" :config="editorConfig" v-model="job_title.skills" placeholder="Enter required skills" />
            </el-form-item></el-col>
        </el-row>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialog = false">Cancel</el-button>
          <el-button type="primary" plain @click="submit('job_title')" :loading="loading">Save</el-button>
        </span>
      </template>
    </el-dialog>

    <el-table ref="tableRef" :data="jobs" v-loading="loading">
      <el-table-column prop="client" label="Client" width="250">
        <template #default="scope">
          <router-link to="#" @click="dialog = true">
            <div style="display: flex; align-items: center">
              <el-avatar v-if="scope.row.company_logo" :src="scope.row.company_logo" />
              <el-avatar v-else>
                {{ $helpers.getInitials(scope.row.client) }}
              </el-avatar>
              <span style="margin-left: 10px">{{ scope.row.client }}<br /></span>
            </div>
          </router-link>
        </template>
      </el-table-column>
      <el-table-column prop="job_title" label="Position" />
      <!-- <el-table-column prop="country" label="Country" />
      <el-table-column prop="location" label="Location" /> -->
      <el-table-column prop="applications" label="Applications">
        <template #default="props">
          <router-link to="#" @click="application(props.row)">{{ props.row.applications }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="shortlisted" label="Shortlisted">
        <template #default="props">
          <router-link to="#" @click="application(props.row)">{{ props.row.shortlisted }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="interviewed" label="Interviewed">
        <template #default="props">
          <router-link to="#" @click="application(props.row)">{{ props.row.interviewed }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="hired" label="Hired">
        <template #default="props">
          <router-link to="#" @click="application(props.row)">{{ props.row.hired }}</router-link>
        </template>
      </el-table-column>
      <el-table-column prop="application_deadline" label="Deadline" :formatter="$helpers.formatDate" />
      <el-table-column prop="status" label="Status" />

      <el-table-column prop="action" label="Action" width="70">
        <template #default="props">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="fas fa-ellipsis-v"></i>
            </span>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="application(props.row)">Applications</el-dropdown-item>
                <el-dropdown-item @click="edit(props.row)">Edit</el-dropdown-item>
                <el-dropdown-item @click="deleteJobTitle(props.row.id)">Delete</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="total, sizes, prev, pager, next" :page-sizes="[10, 25, 50, 100]"
      :total="pagination.meta ? pagination.meta.total : 0" @size-change="handleSizeChange"
      @current-change="handleCurrentChange" />
  </el-card>
</template>

<script>
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import JobService from "../../services/company.service";
export default {
  data() {
    return {
      dialog: false,
      loading: false,
      job_title: {},
      pagination: { page: 1, size: 10 },
      search: "",
      jobs: [],
      editor: ClassicEditor,
      editorConfig: {}
    };
  },
  mounted() {
    this.$store.dispatch("dictionary/getCountries");
    this.$store.dispatch("dictionary/getIndustries");
    this.$store.dispatch("company/getCompanyBranches");
    this.$store.dispatch("dictionary/getDepartments");
    this.$store.dispatch("company/getEmploymentTypes");
    this.$store.dispatch("company/getJobTitles");
    this.getAllJobs();
  },
  computed: {
    job_titles() {
      this.loading = true;
      if (this.$store.state.company.job_titles != undefined) {
        this.loading = false;
        return this.$store.state.company.job_titles;
      }
    },
  },
  methods: {
    edit(data) {
      this.job_title = data;
      this.dialog = true;
    },
    application(data) {
      localStorage.setItem("position", JSON.stringify(data));
      this.$router.push("/applications");
    },
    handleCurrentChange(val) {
      this.pagination.page = val;
      this.getAllJobs();
    },
    handleSizeChange(val) {
      this.pagination.size = val;
      this.getAllJobs();
    },
    getAllJobs() {
      this.loading = true;
      JobService.getAllJobs({
        company_id: this.job_title.company_id,
        page: this.pagination.page,
        size: this.pagination.size,
        search: this.search,
      }).then(
        (res) => {
          this.jobs = res.data;
          this.pagination.meta = res.meta;
          this.loading = false;
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.$store.dispatch("company/saveJobTitle", this.job_title).then(
            (res) => {
              this.job_title = {};
              this.$store.dispatch("company/getJobTitles");
              this.dialog = false;
              this.loading = false;
              this.$notify({
                title: "Success",
                message: res.data.message,
                type: "success",
              });
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              this.$notify({
                title: "Error",
                message: this.message,
                type: "error",
              });
            }
          );
        } else {
          return false;
        }
      });
    },

    deleteJobTitle(id) {
      if (confirm("Do you really want to delete this record?")) {
        this.$store
          .dispatch("company/deleteJobTitle", id)
          .then((response) => {
            this.$store.dispatch("company/getJobTitles");
            this.$notify({
              title: "Success",
              message: response.data.message,
              type: "success",
            });
          })
          .catch((error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();

            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          });
      }
    },
  },
};
</script>